    /* The Modal (background) */
    .modal {
        display: none;
        position: fixed;
        z-index: 101;
        padding-top: 100px;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        background-color: rgba(0,0,0,0.8)
    }
    /* Modal Content */
    .modal-content {
        position: relative;
        margin: auto;
        padding: 0;
        width: 90%
    }
    /* The Close Button */
    .close {
        color: white;
        position: absolute;
        top: 10px;
        right: 25px;
        font-size: 35px;
        font-weight: bold;
        transition: 0.6s ease
    }

    .close:hover,
    .close:focus {
        color: $Primary-colour;
        text-decoration: none;
        cursor: pointer;
        transition: 0.6s ease
    }

    .mySlides {
        display: none
    }

    .cursor {
        cursor: pointer
    }
    /* Next & previous buttons */
    .prev,
    .next {
        cursor: pointer;
        position: absolute;
        top: 55%;
        width: auto;
        padding: 16px;
        margin-top: -50px;
        color: white;
        font-weight: bold;
        font-size: 20px;
        transition: 0.6s ease;
        border-radius: 0 3px 3px 0;
        user-select: none;
        -webkit-user-select: none
    }
    /* Position the "next button" to the right */
    .next {
        right: 0;
        border-radius: 3px 0 0 3px
    }
    /* On hover, add a black background color with a little bit see-through */
    .prev:hover,
    .next:hover {
        color: $Primary-colour
    }
    //image cropping
    .thumbnail {
        position: relative;
        width: 200px;
        height: 200px;
        overflow: hidden
    }

    .thumbnail img {
        position: absolute;
        left: 50%;
        top: 50%;
        height: 100%;
        width: auto;
        -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%)
    }
